<template>
	<view class="container">
		<view class="header">
			<view class="info-box">
				<view class="info">
					<view class="t1">账户余额（元）</view>
					<view class="t2">{{userinfo.account.user_money}}</view>
					<view class="t3">累计收益{{userinfo.account.accumulate_money}}元</view>
				</view>
				<navigator class="btn" hover-class="none" url="./tixian">
					提现
				</navigator>
			</view>
			<image src="../../static/images/bg1.png" class="bg" mode="scaleToFill"></image>
		</view>
		<view class="records" v-if="records.length>0">
			<view class="title">提现记录</view>
			<view class="item" v-for="(item,index) in records" :key="index">
				<view class="info">
					<view class="t1">提现</view>
					<view class="t2">{{item.payment_time}}</view>
				</view>
				<view class="money">+￥{{item.money}}</view>
			</view>
		</view>
		<view class="records" v-else>
			<view class="title">提现记录</view>
			<view class="item">
				 <text style="font-size: 24rpx; color: #999">暂无记录</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userinfo: {
					account:{
						user_money:0,
						accumulate_money:0
					}					
				},
				records:[]
			}
		},
		onLoad() {
			this.getRecords()
		},
		onShow() {
			this.getUserInfo()
			
		},
		components: {
			
		},
		methods: {
			async getUserInfo() {
				let res = await this.$u.get('/v2/member/member/view', {
					id: this.vuex_user.id
				})
				this.userinfo = res.data
			},
			async getRecords() {
				let res = await this.$u.get('/v2/shop/record/index', {
					uid: this.vuex_user.id
				})
				this.records = res.data.data
			},
		}
	}
</script>
<style>
	page {}
</style>
