		width: 750rpx;
		height: 332rpx;
	}
	.header .info-box {
		position: absolute;
		width: 700rpx;
		height: 200rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		left: 25rpx;
		top: 35rpx
	}

	.header .info-box .info .t1 {
		font-size: 24rpx;
		color: #fff;
	}

	.header .info-box .info .t2 {
		font-size: 60rpx;
		color: #fff;
		margin: 20rpx 0;
	}

	.header .info-box .info .t3 {
		color: rgba(255, 255, 255, 0.7);
		font-size: 24rpx;
	}

	.header .info-box .btn {
		width: 160rpx;
		height: 60rpx;
		border-radius: 17rpx;
		background-color: rgb(255, 255, 255, 0.6);
		color: #016DDE;
		font-size: 26rpx;
		text-align: center;
		line-height: 60rpx;
	}

	.records {
		background-color: #fff;
		border-top-right-radius: 40rpx;
		border-top-left-radius: 40rpx;
		position: relative;
		margin-top: -40rpx;
	}
	.records .title{
		font-size: 30rpx;
		color: #333333;
		padding: 20rpx 30rpx;
	}
	.records .item{
		display: flex;
